.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.footer{
  height: 100%;
  background-color:black;
  opacity: 0.9;
}
.particles{
  position:fixed;
  top:0;
  right:0;
  bottom:0;
  left:0;
  z-index:-1;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.about{
  height: 100vh;
  background-color:dimgrey;
  opacity: 0.8;
}
.pr{
  height: 100%;
  background-color:dimgrey;
  opacity: 0.85;
}
.pc{
  height: 100%;
  background-color:dimgrey;
  opacity: 0.85;
}
.contact{
  height: 100%;
  background-color:dimgrey;
  opacity: 0.85;
}
.services{
  height:100vh;
  background-color: lightskyblue;
  opacity: 0.7;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
